<template>
  <form @submit.prevent="start" @keydown.enter.prevent>
    <h1 class="mt-4 form-title">{{ title }}</h1>
    <b-container>
      <hr>
      <b-form-row>
        <b-form-group class="col-md-3" label="Space">
          <b-form-select v-model="space" :options="spaces" required :disabled="isRunning">
            <template v-slot:first>
              <option value="null" disabled>--- Please select an option ---</option>
            </template>
          </b-form-select>
        </b-form-group>
        <b-form-group class="col-md-3" label="Algorithm">
          <b-form-select v-model="algorithm" :options="algorithms" required :disabled="isRunning">
            <template v-slot:first>
              <option value="null" disabled>--- Please select an option ---</option>
            </template>
          </b-form-select>
        </b-form-group>
        <b-form-group class="col-md-3" label="Adaptivity">
          <b-form-select v-model="adaptivity" :options="adaptivities" required :disabled="isRunning">
            <template v-slot:first>
              <option value="null" disabled>--- Please select an option ---</option>
            </template>
          </b-form-select>
        </b-form-group>
        <b-form-group class="col-md-3" label="Partitioning">
          <b-form-select v-model="partitioning" :options="partitionings" required :disabled="isRunning">
            <template v-slot:first>
              <option value="null" disabled>--- Please select an option ---</option>
            </template>
          </b-form-select>
        </b-form-group>
        <b-form-group class="col-md-3" label="Sample size">
          <b-input-group>
            <b-input type="number" max="100000" v-model="treeNumber" required :disabled="isRunning">
            </b-input>
          </b-input-group>
        </b-form-group>
        <b-form-group class="col-md-3" label="Partitions">
          <b-input-group>
            <b-input type="text" v-model="parts" required :disabled="isRunning">
            </b-input>
          </b-input-group>
        </b-form-group>
        <b-form-group class="col-md-3" label="Distance">
          <b-form-select v-model="distance" :options="distances" required :disabled="true">
          </b-form-select>
        </b-form-group>
        <b-form-group class="col-md-3" label="Dataset">
          <b-form-select v-model="dataset" :options="datasets" required :disabled="isRunning">
            <template v-slot:first>
              <option value="null" disabled>--- Please select an option ---</option>
            </template>
          </b-form-select>
        </b-form-group>
      </b-form-row>
      <b-form-row v-if="adaptivityIsNotStatic">
        <b-form-group class="col-md-4" label="Adaptation Range">
          <b-input-group>
            <b-form-input type="number" v-model="adapt.range" min="0.01" step="0.01" required :disabled="isRunning">
            </b-form-input>
          </b-input-group>
        </b-form-group>
        <b-form-group v-if="adaptivity === 'advanced'" class="col-md-4" label="Adaptation Queue">
          <b-input-group>
            <b-form-input min="1" type="number" v-model="adapt.queue" step="1" required :disabled="isRunning">
            </b-form-input>
          </b-input-group>
        </b-form-group>
        <b-form-group class="col-md-4" label="Adaptation Cost Function">
          <b-form-select v-model="adapt.cost_function" :options="cost_functions" required :disabled="isRunning">
          </b-form-select>
        </b-form-group>
        <b-form-group class="col-md-4" label="Adaptation Overload">
          <b-input-group>
            <b-form-input type="number" v-model="adapt.overload" min="101" max="200" step="1" required :disabled="isRunning">
            </b-form-input>
          </b-input-group>
        </b-form-group>
        <b-form-group v-if="adaptivity === 'advanced'" class="col-md-4" label="Adaptation Underload">
          <b-input-group>
            <b-form-input type="number" v-model="adapt.underload" min="10" max="99" step="1" required :disabled="isRunning">
            </b-form-input>
          </b-input-group>
        </b-form-group>
        <b-form-group class="col-md-4" label="Adaptation Buffer Period">
          <b-input-group>
            <b-form-input type="number" v-model="adapt.buffer_period" min="0.01" step="0.01" required :disabled="isRunning">
            </b-form-input>
          </b-input-group>
        </b-form-group>
      </b-form-row>
      <div v-if="space">
        <b-form-row>
          <div>Windowing parameters</div>
        </b-form-row>
        <b-form-row>
          <b-form-group class="col-md-6" label="W (ms)">
            <b-input-group>
              <b-form-input :type="isMultiAll ? 'text' : 'number'" v-model="w" :state="wState" required :disabled="isRunning">
              </b-form-input>
              <b-form-invalid-feedback>
                <template v-if="isMultiAll">
                  W must consist of integers, separated with the semicolon (;) delimiter
                </template>
                <template v-else>
                  W must be greater than 0
                </template>
              </b-form-invalid-feedback>
            </b-input-group>
          </b-form-group>
          <b-form-group class="col-md-6" label="S (ms)">
            <b-input-group>
              <b-form-input :type="isMultiAll ? 'text' : 'number'" v-model="s" :state="sState" required :disabled="isRunning">
              </b-form-input>
              <b-form-invalid-feedback>
                <template v-if="isMultiAll">
                  S must consist of integers, separated with the semicolon (;) delimiter. In addition, two conditions must be satisfied, max(S) &lt;= min(W) and W % S = 0 for every W, S pair.
                </template>
                <template v-else>
                  S must be greater than 0
                </template>
              </b-form-invalid-feedback>
            </b-input-group>
          </b-form-group>
        </b-form-row>
        <b-form-row>
          <div>Application parameters</div>
        </b-form-row>
        <b-form-row>
          <b-form-group class="col-md-6" label="k">
            <b-input-group>
              <b-form-input :type="isSingle ? 'number' : 'text'" v-model="k" :state="kState" required :disabled="isRunning">
              </b-form-input>
              <b-form-invalid-feedback>
                <template v-if="isSingle">
                  k must be greater than 0
                </template>
                <template v-else>
                  k must consist of integers, separated with the semicolon (;) delimiter
                </template>
              </b-form-invalid-feedback>
            </b-input-group>
          </b-form-group>
          <b-form-group class="col-md-6" label="R">
            <b-input-group>
              <b-form-input :type="isSingle ? 'number' : 'text'" v-model="r" :step="isSingle ? '0.01' : ''" :state="rState" required :disabled="isRunning">
              </b-form-input>
              <b-form-invalid-feedback>
                <template v-if="isSingle">
                  R must be greater than 0
                </template>
                <template v-else>
                  R must consist of doubles, separated with the semicolon (;) delimiter
                </template>
              </b-form-invalid-feedback>
            </b-input-group>
          </b-form-group>
        </b-form-row>
      </div>
      <hr>
      <div class="d-flex flex-row-reverse">
          <b-button variant="primary" type="submit" :disabled="invalid || isRunning">
            <b-spinner v-if="isRunning" small></b-spinner>
            Start
          </b-button>
          <b-button variant="danger" @click="stop" :disabled="!isRunning" class="mr-1">
            Stop
          </b-button>
      </div>
      <b-row>
        <b-alert variant="info" show v-if="isRunning" class="col-md-12 mt-3">
          <h2>Job is running, press stop to terminate it</h2>
        </b-alert>
      </b-row>
    </b-container>
  </form>
</template>

<script>
import axios from 'axios'

export default {
  name: 'MainForm',
  props: {
    title: String
  },
  data: () => {
    return {
      isRunning: false,
      space: null,
      algorithm: null,
      adaptivity: null,
      partitioning: null,
      treeNumber: 10000,
      dataset: null,
      parts: '4',
      distance: 'euclidean',
      w: '',
      s: '',
      k: '',
      r: '',
      adaptivities: [
        { value: 'static', text: 'Static' },
        { value: 'naive', text: 'Naive' },
        { value: 'advanced', text: 'Advanced' },
      ],
      distances: [
        { value: 'euclidean', text: 'Euclidean' },
      ],
      datasets: [
        { value: 'STK', text: 'STK' },
        { value: 'TAO', text: 'TAO' },
      ],
      spaces: [
        { value: 'single', text: 'Single' },
        { value: 'multi-app', text: 'Multi-App' },
        { value: 'multi-all', text: 'Multi-All' },
      ],
      cost_functions: [
        { value: '1', text: 'Number of non-replicas' },
        { value: '2', text: 'Processing time' },
        { value: '3', text: 'Number of non-replicas * Processing time' },
      ],
      adapt: {
        range: '1',
        queue: '5',
        cost_function: '1',
        overload: '130',
        underload: '30',
        buffer_period: '1'
      }
    }
  },
  computed: {
    algorithms () {
      switch (this.space) {
        case 'single':
          return [
            { value: 'advanced', text: 'Advanced Extended' },
            { value: 'cod', text: 'COD' },
            { value: 'slicing', text: 'Slicing' },
            { value: 'pmcod', text: 'PMCOD' },
            { value: 'pmcod_net', text: 'PMCOD-net' },
          ]
        case 'multi-app':
          return [
            { value: 'amcod_rk', text: 'AMCOD' },
            { value: 'psod_rk', text: 'PSOD' },
            { value: 'sop_rk', text: 'SOP' },
            { value: 'pmcsky_rk', text: 'PMCSKY' },
          ]
        case 'multi-all':
          return [
            { value: 'psod_rkws', text: 'PSOD' },
            { value: 'sop_rkws', text: 'SOP' },
            { value: 'pmcsky_rkws', text: 'PMCSKY' },
          ]
        default:
          return []
      }
    },
    partitionings () {
      if (this.adaptivity == null) return []
      switch (this.adaptivity) {
        case 'static':
          return [
            { value: 'grid', text: 'Grid' },
            { value: 'tree', text: 'Tree' },
          ]
        default:
          return [
            { value: 'tree', text: 'Tree' }
          ]
      }
    },
    adaptivityIsNotStatic () {
      return this.adaptivity && this.adaptivity !== 'static'
    },
    isTree () {
      return this.partitioning === 'tree'
    },
    isSingle () {
      return this.space === 'single'
    },
    isMultiAll () {
      return this.space === 'multi-all'
    },
    isMulti () {
      if (this.space === null) return false
      return this.space.startsWith('multi-')
    },
    wArray () {
      return this.w.split(';').map(Number)
    },
    sArray () {
      return this.s.split(';').map(Number)
    },
    kArray () {
      return this.k.split(';').map(Number)
    },
    rArray () {
      return this.r.split(';').map(Number)
    },
    wState () {
      if (this.isMulti) {
        return this.validateIntegerArray(this.wArray) && this.wArray[0] !== 0
      }
      if (this.w.length === 0 || this.w <= 0) return false
      return true
    },
    sState () {
      if (this.isMulti) {
        return this.validateIntegerArray(this.sArray) && this.validateSArray()
      }
      if (this.s.length === 0 || this.s <= 0) return false
      return true
    },
    kState () {
      if (this.isMulti) {
        return this.validateIntegerArray(this.kArray) && this.kArray[0] !== 0
      }
      if (this.k.length === 0 || this.k <= 0) return false
      return true
    },
    rState () {
      if (this.isMulti) {
        return this.validateDoubleArray(this.rArray) && this.rArray[0] !== 0
      }
      if (this.r.length === 0 || this.r <= 0) return false
      return true
    },
    maxS () {
      return Math.max(...this.sArray)
    },
    minW () {
      return Math.min(...this.wArray)
    },
    invalid () {
      if (this.space === null ||
          this.algorithm === null ||
          this.partitioning === null ||
          this.dataset === null) return true
      if (!this.wState ||
          !this.sState ||
          !this.kState ||
          !this.rState) return true
      return false
    }
  },
  watch: {
    space: function () {
      this.algorithm = null
      this.partitioning = null
      this.treeNumber = 10000
      this.dataset = null
      this.w = ''
      this.s = ''
      this.k = ''
      this.r = ''
    },
    algorithm: function () {
      this.partitioning = null
      this.treeNumber = 10000
      this.dataset = null
    }
  },
  methods: {
    async start () {
      await axios.post('http://localhost:8000/start', {
        // space: this.translateSpace(),
        algorithm: this.algorithm,
        partitioning: this.partitioning,
        treeNumber: this.treeNumber,
        dataset: this.dataset,
        w: this.w,
        s: this.s,
        r: this.r,
        k: this.k,
        partitions: this.parts,
        distance: this.distance,
        adaptivity: this.adaptivity,
        ...this.adapt
      })
              .then(() => {
                this.isRunning = true
                // console.warn(response)
              })
              .catch(() => {
                this.isRunning = false
                //console.log(error)
                // if (error.response) {
                //
                // }
              })
    },
    async stop () {
      await axios.post('http://localhost:8000/stop')
              .then(() => {
                this.isRunning = false
                // console.warn(response)
              })
              .catch(() => {
                this.isRunning = false
                //console.log(error)
                // if (error.response) {
                //
                // }
              })
    },
    validateIntegerArray (arr) {
      for (let i = 0; i < arr.length; ++i) {
        if (!Number.isInteger(arr[i])) return false
      }
      return true
    },
    validateDoubleArray (arr) {
      for (let i = 0; i < arr.length; ++i) {
        if (isNaN(arr[i])) return false
      }
      return true
    },
    validateSArray () {
      if (this.maxS > this.minW) return false
      for (let i = 0; i < this.wArray.length; ++i){
        for (let j = 0; j < this.sArray.length; ++j){
          if (this.wArray[i] % this.sArray[j] !== 0) return false
        }
      }
      return true

    },
    translateSpace () {
      if (this.space === 'multi-app') return 'rk'
      if (this.space === 'multi-all') return 'rkws'
      return 'single'
    }

  }
}
</script>

